.App {
    max-width: 1024px;
    margin: 0 auto;
    padding: 50px 20px;
}

.container {
    max-width: 768px;
    margin: 0 auto;
}

.cards-list {
    display: grid;
    grid-gap: 24px;
    width: 100%;
}

@media (min-width: 600px) {
    .cards-list {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 900px) {
    .cards-list {
        grid-template-columns: repeat(3, 1fr);
    }
}

.primary-button {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 28px;
    outline: none;
    border: none;
    color: #ffffff;
    min-width: 80px;
    height: 30px;

    background: #0788ff;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.14);
    border-radius: 40px;
    transition: all 0.3s ease;
}
.primary-button:hover {
    background: #0667bf;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.14);
}
